
import Box from 'vux/src/components/box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Card from 'vux/src/components/card/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Alert from 'vux/src/components/alert/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'


export default {

components: {
Box,
Group,
XButton,
Flexbox,
FlexboxItem,
Card,
XDialog,
XInput
},
data () {
var wallet = this.getWallet(this.$route.params.name);
if(!wallet) {
AlertModule.show('');
return false;
}
var coinName = this.$route.params.coinName;
console.log("coinname", coinName);

return {
wallet:wallet,
coinName: coinName,
password:'',
amount:'',
gas:'',
address:''





}
},
methods: {
validate:function () {
if(!this.password) {
AlertModule.show('')
return false
}
if(!this.amount) {
AlertModule.show('')
return false
}
if(!this.address) {
AlertModule.show('')
return false
}
if(!this.gas) {
AlertModule.show('')
return false
}

if( (parseFloat(this.gas)) < 21000) {
AlertModule.show(' 21000 wei ')
return false
}






return true
},
confirmTran:function() {
if(!this.validate()) {
return false
}


this.$vux.loading.show('...');
var that = this;
setTimeout(function(){
that.sendCoinTran(that.coinName, that.wallet,that.address,that.amount,that.gas, that.password, null, function(err, hash){
that.$vux.loading.hide();
if(err) {
console.log('', err)
if(err.message==='invalid address') {
AlertModule.show('')
} else if (err.message==='base fee exceeds gas limit') {
AlertModule.show('')
} else {
AlertModule.show(''+err.message)
}
return false;
}

AlertModule.show('hash'+hash+'')
});

},200);
}
}
}



